/*clases globales*/
img {
  width: 100%;
  height: auto;
}

.hover::before, .hover::after {
  content: '';
  height: 14px;
  width: 14px;
  position: absolute;
  -webkit-transition: all .35s ease;
  transition: all .35s ease;
  opacity: 0;
}

.hover::before {
  content: '';
  right: 0;
  top: 0;
  border-top: 3px solid white;
  border-right: 3px solid white;
  -webkit-transform: translate(-100%, 50%);
          transform: translate(-100%, 50%);
}

.hover:after {
  content: '';
  left: 0;
  bottom: 0;
  border-bottom: 3px solid white;
  border-left: 3px solid white;
  -webkit-transform: translate(100%, -50%);
          transform: translate(100%, -50%);
}

.hover:hover:before, .hover:hover:after {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  opacity: 1;
}

#Inicio, #Nasa, #planetas {
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#planetas, #Nasa, #Inicio {
  padding: 100px 3rem 0 3rem;
}

.margin {
  margin-bottom: 2rem;
}

h2, .titulos {
  font-size: clamp(1rem, 5vw, 4rem);
  margin-top: 3rem;
  margin-bottom: 2rem;
}

h3 {
  font-size: clamp(0.8rem, 3vw, 3rem);
}

h4 {
  font-size: clamp(0.6rem, 2vw, 2rem);
}

p {
  font-size: clamp(0.5rem, 1.5vw, 1.1rem);
}

/*Nav*/
nav {
  min-height: 100vh;
  min-width: 320px;
  max-width: 1920px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  min-height: -webkit-max-content;
  min-height: -moz-max-content;
  min-height: max-content;
  top: 0;
  left: 0;
  right: 0;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 1rem 5rem;
  background-color: transparent;
  z-index: 3;
  background: rgba(4, 4, 4, 0.534);
  -webkit-backdrop-filter: blur(4px);
          backdrop-filter: blur(4px);
}

nav .logo {
  color: #e2e2e2;
  letter-spacing: 5px;
  font-size: 20px;
}

nav .nav-links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  font-weight: 600;
}

nav .nav-links* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-transition: all .35s ease;
  transition: all .35s ease;
}

nav .nav-links li {
  display: inline-block;
  list-style: outside none none;
  margin: .5em 1em;
  padding: 0;
}

nav .nav-links a {
  padding: .5em .8em;
  color: #e2e2e2;
  position: relative;
  text-decoration: none;
  font-size: 20px;
}

nav .nav-active {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

nav #hamburguesa {
  display: none;
  cursor: pointer;
}

nav #hamburguesa div {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 5px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

nav #hamburguesa .linea1 {
  -webkit-transform: rotate(-45deg) translate(-5px, 6px);
          transform: rotate(-45deg) translate(-5px, 6px);
}

nav #hamburguesa .linea2 {
  opacity: 0;
}

nav #hamburguesa .linea3 {
  -webkit-transform: rotate(45deg) translate(-5px, -6px);
          transform: rotate(45deg) translate(-5px, -6px);
}

