@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
}
$color-texto:rgb(226,226,226);

@mixin width-height {
    min-height: 100vh;
    min-width: 320px;
    max-width: 1920px;
    margin: 0 auto;
}


body{
    @include width-height;
    font-family: 'Poppins', sans-serif;
}

/*clases globales*/

img{
    width: 100%;
    height: auto;
}
.hover{
    &::before,
    &::after {
        content: '';
        height: 14px;
        width: 14px;
        position: absolute;
        transition: all .35s ease;
        opacity: 0;
    }
    &::before {
        content: '';
        right: 0;
        top: 0;
        border-top: 3px solid white;
        border-right: 3px solid white;
        transform: translate(-100%, 50%);
    }
    &:after {
        content: '';
        left: 0;
        bottom: 0;
        border-bottom: 3px solid white;
        border-left: 3px solid white;
        transform: translate(100%, -50%)
    }
    &:hover:before,
    &:hover:after{
      transform: translate(0,0);
      opacity: 1;
    }
}
#Inicio, #Nasa, #planetas{
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
#planetas, #Nasa, #Inicio{
    padding: 100px 3rem 0 3rem;
}
.margin{
    margin-bottom:2rem ;
}

h2,.titulos{
    font-size: clamp(1rem, 5vw, 4rem);
    margin-top: 3rem;
    margin-bottom: 2rem;
}
h3{
    font-size: clamp(.8rem, 3vw, 3rem);
}
h4{
    font-size: clamp(.6rem, 2vw, 2rem);
}
p{
    font-size: clamp(.5rem, 1.5vw, 1.1rem);
}
//---------------------------------------------

/*Nav*/

nav{
    @include width-height;
    display: flex;
    position: fixed;
    min-height: max-content;
    top: 0;
    left: 0;
    right: 0;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5rem;
    background-color: transparent;
    z-index: 3;
    background: rgba(4, 4, 4, 0.534);
    backdrop-filter: blur(4px);
    .logo{
        color: $color-texto;
        letter-spacing: 5px;
        font-size: 20px;
    }
    .nav-links{
        display: flex;
        justify-content: space-between;
        text-align: center;
        font-weight: 600;
        &*{
            box-sizing: border-box;
            transition: all .35s ease;
        }
          
        li {
            display: inline-block;
            list-style: outside none none;
            margin: .5em 1em;
            padding: 0;
        }
          
        a{
            padding: .5em .8em;
            color: $color-texto;
            position: relative;
            text-decoration: none;
            font-size: 20px;
        }   
    }
    .nav-active{
        transform: translateX(0%);
    }
    #hamburguesa{
        display: none;
        cursor: pointer;
        div{
            width:25px;
            height: 3px;
            background-color: white;
            margin: 5px;
            transition: all 0.3s ease;
        }
        .linea1{
            transform: rotate(-45deg) translate(-5px, 6px);
        }
        .linea2{
            opacity: 0;
        }
        .linea3{
            transform: rotate(45deg) translate(-5px, -6px);
        }
    }
} 

