//--------------------------------------------------------------------------------------

/*Planetas*/
#planetas{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(/public/imagenes/fondo3.jpg);
    color: $color-texto;
    gap: 3rem;
    .div-section{
        display: flex;
        letter-spacing: 3px;
        width: 100%;
        gap: 3rem;
        position: relative;
        .div-img{
            position: fixed;
            top: 50px;
            padding-right: 3rem;
        }
        .descripcion{
            align-self: center;
        }
        .lista{
            margin-bottom: 1rem;
            margin-left: 2rem;
        } 
        div{
            width: 50%;
        }
        .planetas{
            cursor: pointer;
            width: min-content;
        }
    }
}


//------------------------------------------------------------

/*Api Nasa*/

#Nasa{
    background-image: url(/public/imagenes/fondo5.jpg);
    color: $color-texto;
    #ImagenAStronomica{
        margin: 0 auto 3rem auto;
        max-width: 1000px;
        position: relative;
        h2{
            text-align: center;
        }
        .div-img{
            display: flex;
            height: 600px;
            margin: 0 auto;
            text-align: center;
            margin-bottom: 1rem;
            img{
                object-fit:cover;
            }
        }
        #descripcion{
            position: absolute;
            bottom: 10px;
            left: 10px;
        }
    }
    #TierraApi{
        .div-img{
            max-width: 600px;
            margin: 0 auto;
            position: relative;
            #siguiente{
                position: absolute;
                top: 45%;
                right: 0;
            }
            #anterior{
                position: absolute;
                top: 45%;
                left: 0;
            }
            #fecha{
                position: absolute;
                bottom: 10px;
                left: 10px;
            }
            button{
                background: none;
                color: white;
                border: none;
                cursor: pointer;
                font-size: clamp(1.5rem, 4vw, 4rem);
                padding: 5px;
            }
        }
        
    }
    #MarteApi{
        margin-top: 3rem;
        .camarasDescripcion{
            display: flex;
            flex-direction: column;
            gap: 2rem;
        }
        .Info{
            margin: 1rem 0;
        }
        .camaras{
            display: grid;
            align-content: center;
            grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));

            .fotos{
                display: flex;
                margin: 0 auto;
                .div-fotos{
                    width: 200px;
                    position: relative;
                    color: white;
                    p{
                        position: absolute;
                        left: 10px;
                        bottom: 10px;
                        font-size: .8rem;
                    }
                }
            }
            
        }
        
    }
}

//Media queris

@media screen and (max-width:1000px) {
    nav{
        padding: 1rem 2.5rem;
    }
    #planetas, #Nasa, #Inicio{
        padding: 100px 2.5rem 0 2.5rem;
    }
    //INICIO
    #Inicio{
        display: grid;
        grid-template-columns: repeat(3, minmax(100px, 250px));
        grid-template-rows: max-content;
        justify-content: center;
        gap: 10px;
        .width{
            width: fit-content!important;
        }
    }
}
 
    @media screen and (max-width:768px) { 
        nav{
            background: rgba(4, 4, 4, 0.911);
            backdrop-filter: blur(4px);
            position: fixed;
        }
        .nav-links{
            position:absolute;
            right: 0px;
            top:8vh;
            height: 92vh;
            width: 100%!important;
            background: rgba(4, 4, 4, 0.911);
            backdrop-filter: blur(4px);
            flex-direction: column;
            align-items: center;
            justify-content: space-around!important;
            transform: translateX(100%);
            transition: transform .5s ease-in;
        }
        .nav-links li{
            opacity: 0;
        }
        #hamburguesa{
            display: block!important;
        }
        #planetas, #Nasa, #Inicio{
            padding: 100px 1.5rem 0 1.5rem;
        }
        nav{
            padding: 1rem 1.5rem;
        }

        //planetas
        #planetas{
            .div-section{
                flex-direction: column;
                align-items: center;
                width: 100%;
                gap: 1rem;
                .relleno{
                    display: none;
                }
                .div-img{
                    position: relative;
                    top: 0;
                    padding-right: 0;
                    width: 70%;
                }
                .descripcion{
                    width: 100%;
                }
            }
            gap: 1rem;
        }
        //api-nasa
        #ImagenAStronomica{
            .div-img{
                height: 400px!important;
            }
        }
}
@media screen and (max-width:426px) { 
    #Inicio{
        grid-template-columns: repeat(2, minmax(100px, 250px));
    }
    #ImagenAStronomica{
        .div-img{
            height: 300px!important;
        }
    }
}

@keyframes navLinkFade{
    from{
        opacity: 0;
        transform:translateX(50px);
    }
    to{
        opacity: 1;
        transform:translateX(0px);
    }
}

 
